body {
  margin: 0;
  padding: 0;
  font-size: 0.75%;
  font-family: "Raleway", sans-serif;
  font-size: 14px;
  box-sizing: border-box;
  color: #6c7279;
  -webkit-font-smoothing: antialiased;

  background-color: rgb(252, 249, 249);
  overflow-x: hidden;
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

p {
  margin: 0 0 10px;
}

.btn {
  outline: none;
  text-decoration: none;
  background-color: #fe7a1a;
  border: 1px solid #fe7a1a;
  padding: 10px 30px;
  color: black;
  text-align: center;
  font-weight: bold;
  font-size: 15px;
  margin-top: 30px;
  transition: all 0.3s linear;
}
.secondary-btn {
  border: 2px solid #fe7a1a;
  background-color: rgb(252, 249, 249);
  transition: all 0.3s linear;
  margin-left: 10px;
}
.btn:hover {
  background-color: rgb(250, 148, 74);
  transform: translateY(5px);
}

/* Header
===================*/
.header {
  width: 100%;
  height: 20vh;
  background-color: #31344e;
  z-index: 10000000000000000000000000000000000000;
  transition: 0.3s;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 30px 5%;
}
.sticky {
  position: sticky;
  top: 0%;
  left: 0%;
  width: 100%;
  height: 12vh;
}

/* Logo */
.sticky-ell {
  width: 40px;
  height: 40px;
}

.sticky-ell-txt {
  font-size: 21px;
}

.header__logo-name {
  display: flex;
  flex-direction: column;
  margin: 0 0 0 20px;
}
/*End of Logo */
/* Social */
.social {
  display: flex;
  justify-content: right;
  right: 0%;
}

.social__item {
  display: flex;
  align-items: center;
  width: 40px;
  height: 40px;
}

.social__icon {
  display: block;
  height: 18px;
  background-color: #31344e;
}
svg {
  fill: white;
}
svg:hover {
  fill: white;
  opacity: 0.8;
}
/* End of Social */

/* End of Header
===================*/
.header__logo {
  display: flex;
  align-items: center;
  text-decoration: none;
}
.header__logo-sign-ellipse {
  width: 50px;
  height: 50px;
  background: #fe7a1a;
  border-radius: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all 0.3s ease;
}
.header__logo-sign-text {
  position: absolute;
  font-weight: medium;
  font-size: 24px;
  color: white;
  transition: all 0.3s ease;
}
.it-1 {
