  font-weight: 600;
  font-size: 18px;
  margin-bottom: 10px;
  transition: all 0.3s ease;
}
.sticky-it-1 {
  font-size: 16px;
}
.it-2 {
  color: rgba(255, 255, 255, 0.877);
  font-weight: 500;
  font-size: 14px;
  transition: all 0.3s ease;
}
.sticky-it-2 {
  font-size: 12px;
}
.title {
  width: 80%;
  margin: 0 auto;
  text-align: center;
}
.heading {
  color: black;
  font-size: 36px;
  letter-spacing: 0.2;
  font-weight: 600;
}
.subtitle {
  color: rgb(94, 94, 94);
  text-align: center;
  margin-top: 20px;
  font-size: 20px;
  font-weight: 500;
}
