/* Navigation */
.nav {
  display: flex;
  text-transform: uppercase;
}

.nav__link {
  margin-left: 20px;
  padding: 1rem;
  color: #fff;
  text-decoration: none;
  font-weight: 500;
  font-size: 14px;
  color: rgba(255, 255, 255, 0.877);
}

.nav__link:first-child {
  margin-left: 0px;
}
.nav__link:last-child {
  margin-right: 30px;
}
.nav .current {
  color: #ff9243;
}
/* End of Navigation */

/* Intro
===================*/

.intro {
  height: 100vh;
  width: 100%;
  background: linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.2)),
    url(./assets/images/banner.jpg) no-repeat center;
  background-size: cover;
  background-attachment: fixed;
}
.intro__inner {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 90vh;
}
.main-btn {
  width: 20%;
  box-shadow: 0px 0px 30px rgb(100, 44, 1);
  border-top-left-radius: 50px;
  border-bottom-right-radius: 50px;
}
.intro__inner:hover .main-btn {
  animation: buttonRotation 2s 1s linear;
}

@keyframes buttonRotation {
  0% {
    transform: scale(1) rotateZ(0);
  }
  5% {
    transform: scale(1.1) rotateZ(5deg);
  }
  10% {
    transform: scale(1.05) rotateZ(-5deg);
  }
  15% {
    transform: scale(1.05) rotateZ(5deg);
  }
  20% {
    transform: scale(1.05) rotateZ(-5deg);
  }
  25% {
    transform: scale(1.05) rotateZ(5deg);
  }
  30% {
    transform: scale(1.05) rotateZ(-5deg);
  }
  35% {
    transform: scale(1.05) rotateZ(5deg);
  }
  40% {
    transform: scale(1.05) rotateZ(-5deg);
  }
  45% {
    transform: scale(1.05) rotateZ(5deg);
  }
  50% {
    transform: scale(1.05) rotateZ(-5deg);
  }
  55% {
    transform: scale(1.05) rotateZ(5deg);
  }
  60% {
    transform: scale(1.05) rotateZ(-5deg);
  }
  65% {
    transform: scale(1.05) rotateZ(5deg);
  }
  70% {
    transform: scale(1.05) rotateZ(-5deg);
  }
  75% {
    transform: scale(1.05) rotateZ(5deg);
  }
  80% {
    transform: scale(1.05) rotateZ(-5deg);
  }
  85% {
    transform: scale(1.05) rotateZ(5deg);
  }
  90% {
    transform: scale(1.05) rotateZ(-5deg);
  }
  95% {
    transform: scale(1.05) rotateZ(5deg);
  }
  100% {
    transform: scale(1) rotateZ(0);
  }
}

.intro__title {
  color: white;
  font-size: 55px;
  text-align: center;
  letter-spacing: 2px;
}
.intro__subtitle {
  color: rgb(201, 221, 250);
  text-align: center;
  margin-top: 20px;
  font-size: 20px;
}
/* End of Intro
===================*/

/* About
===================*/
.about {
  width: 100%;
  height: 90vh;
}
.about-container {
  height: 100%;
  position: relative;
  background-color: rgb(252, 249, 249);
  padding-bottom: 300px;
}
/* Main section */
.about-inner {
  display: flex;
  position: absolute;
  top: 25%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 75%;
  height: 80vh;
  text-align: left;
  padding: 55px;
  background-color: white;
  box-shadow: 0px 0px 30px rgb(201, 201, 201);
}
.about-text {
  width: 50%;
  padding-left: 50px;
}
.img-box {
  width: 40%;
}
.about-img {
  max-width: 84.1%;
  height: auto;
}

.about-greetings {
  background: -webkit-gradient(
    linear,
    left top,
    right top,
    from(#38d39f),
    to(#38a4d3)
  );
  background: linear-gradient(left, #38d39f, #38a4d3);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 40px;
  letter-spacing: 0.2;
  font-weight: 600;
}
.about-par {
  padding-top: 20px;
  padding-bottom: 20px;
  font-size: 18px;
  font-weight: 400;
}
.about-svg-anim {
  width: 400px;
  position: absolute;
  background-color: transparent;
  top: 63%;
  left: 63%;
}
/* ABOUT SVG ANIMATION */
#first-sqr {
  animation: f-sqr-rotation 5s infinite linear;
  transform-origin: center;
  transform-box: fill-box;
}

@keyframes f-sqr-rotation {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(1800deg);
  }
}

#second-sqr {
  animation: s-sqr-rotation 4s infinite linear;
  transform-origin: center;
  transform-box: fill-box;
}

@keyframes s-sqr-rotation {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(-1800deg);
  }
}
#third-sqr {
  animation: t-sqr-rotation 2s infinite ease-out;
  transform-origin: center;
  transform-box: fill-box;
}

@keyframes t-sqr-rotation {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
#or-sqr {
  animation: or-sqr 1s infinite linear;
}
@keyframes or-sqr {
  0% {
    transform: translate(0px, 0px);
  }
  33% {
    transform: translate(5px, 10px);
  }
  66% {
    transform: translate(-5px, -10px);
  }
  100% {
    transform: translate(0px, 0px);
  }
}
#arm-svg {
  animation: arm-anim 4s infinite ease;
  transform-origin: bottom;
  transform-box: fill-box;
}
@keyframes arm-anim {
  0% {
    transform: rotateZ(0deg);
  }
  33% {
    transform: rotateZ(15deg);
  }
  66% {
    transform: rotateZ(-15deg);
  }
  100% {
    transform: rotateZ(0deg);
  }
}
#head-svg {
  animation: arm-anim 4s infinite ease-in-out;
  transform-origin: bottom;
  transform-box: fill-box;
}
@keyframes arm-anim {
  0% {
    transform: rotateZ(0deg);
  }
  33% {
    transform: rotateZ(10deg);
  }
  66% {
    transform: rotateZ(-15deg);
  }
  100% {
    transform: rotateZ(0deg);
  }
}
#backgr-1 {
  animation: background 3s infinite ease-in-out;
}
@keyframes background {
  0% {
    transform: translateX(0px);
  }
  50% {
    transform: translateX(50px);
  }
  100% {
    transform: translateX(0px);
  }
}
#text-l1 {
  animation: text 3s infinite linear;
}

#text-l2 {
  animation: text 4s infinite ease;
}
#text-l3 {
  animation: text 5s infinite ease-in-out;
}

#text-l4 {
  animation: text 3s infinite ease-in;
}

#text-l5 {
  animation: text 2s infinite ease-out;
}
@keyframes text {
  0% {
    transform: translateX(0px);
  }
  50% {
    transform: translateX(-50px);
    width: 30%;
  }
  100% {
    transform: translateX(0px);
  }
}
#circle {
  animation: circle 6s infinite linear;
}
@keyframes circle {
  0% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(-50px);
  }
  100% {
    transform: translateY(0px);
  }
}
#text-btn {
  animation: text-btn 5s infinite linear;
  transform-box: fill-box;
}
@keyframes text-btn {
  0% {
    transform: translateX(0px);
  }
  50% {
    transform: scale(1.5);
  }
  100% {
    transform: translateX(0px);
  }
}
/* END OF ABOUT SVG ANIMATION */

/* End of About
===================*/

/* Services
===================*/
/* SVG */
#dot1 {
  animation: dots 0.8s infinite ease-in-out;
}
#dot2 {
  animation: dots 0.9s infinite ease-in-out;
}
#dot3 {
  animation: dots 1s infinite ease-in-out;
}
#center {
  animation: clock 2s infinite linear;
  transform-origin: bottom;
  transform-box: fill-box;
  z-index: 10;
  background-color: white;
}

@keyframes dots {
  0% {
    transform: translateX(-10px);
  }
  50% {
    transform: translateX(0px);
  }
  100% {
    transform: translateX(-10px);
  }
}
@keyframes clock {
  from {
    transform: rotateZ(0deg);
  }
  to {
    transform: rotateZ(360deg);
  }
}
@keyframes right {
  from {
    transform: rotateZ(0deg);
  }
  to {
    transform: rotateZ(-360deg);
  }
}

#pencil {
  animation: pen 4s infinite linear;
  transform-origin: top;
}
#text-top {
  animation: text-top 3.5s 0.5s infinite linear;
}
#text-bottom {
  animation: text-bottom 3.5s 0.5s infinite linear;
}
#text-img {
  animation: text-img 3.5s 0.5s infinite linear;
}
#img-alert {
  animation: img-alert 3.5s 0.5s infinite linear;
}
#dot-top1 {
  animation: dots-top 0.8s infinite ease-in-out;
}
#dot-top2 {
  animation: dots-top 0.9s infinite ease-in-out;
}
#dot-top3 {
  animation: dots-top 1s infinite ease-in-out;
}

@keyframes pen {
  0% {
    transform: rotate(-10deg);
  }
  50% {
    transform: rotate(10deg);
  }
  100% {
    transform: rotate(-10deg);
  }
}
@keyframes text-top {
  0% {
    transform: translateX(-100px);
    opacity: 0;
  }
  50% {
    transform: translateX(-25px);
    opacity: 0.5;
  }
  100% {
    transform: translateX(0px);
    opacity: 1;
  }
}
@keyframes text-bottom {
  0% {
    transform: translateX(100px);
    opacity: 0;
  }
  50% {
    transform: translateX(25px);
    opacity: 0.5;
  }
  100% {
    transform: translateX(0px);
    opacity: 1;
  }
}
@keyframes text-img {
  0% {
    transform: translateY(100px);
    opacity: 0;
  }
  50% {
    transform: translateY(25px);
    opacity: 0.5;
  }
  100% {
    transform: translateY(0px);
    opacity: 1;
  }
}
@keyframes img-alert {
  0% {
    transform: translateY(-100px);
    opacity: 0;
  }
  50% {
    transform: translateY(-25px);
    opacity: 0.5;
  }
  100% {
    transform: translateY(0px);
    opacity: 1;
  }
}
@keyframes dots-top {
  0% {
    transform: translateY(-10px);
  }
  50% {
    transform: translateY(0px);
  }
  100% {
    transform: translateY(-10px);
  }
}

#elem-1 {
  animation: move4one 5s infinite linear;
}
@keyframes move4one {
  0% {
    transform: translateY(0px);
  }
  25% {
    transform: translateY(40px);
  }
  50% {
    transform: translateY(-60px);
  }
  75% {
    transform: translateY(100px);
  }
  100% {
    transform: translateY(0px);
  }
}
#elem-2 {
  animation: move4two 5s infinite linear;
}
@keyframes move4two {
  0% {
    transform: translateY(0px);
  }
  25% {
    transform: translateY(-100px);
  }
  50% {
    transform: translateY(20px);
  }
  75% {
    transform: translateY(-60px);
  }
  100% {
    transform: translateY(0px);
  }
}
#elem-3 {
  animation: move4three 5s infinite linear;
}
@keyframes move4three {
  0% {
    transform: translateY(0px);
  }
  25% {
    transform: translateY(-30px);
  }
  50% {
    transform: translateY(100px);
  }
  75% {
    transform: translateY(-80px);
  }
  100% {
    transform: translateY(0px);
  }
}
#elem-4 {
  animation: move4four 5s infinite linear;
}
@keyframes move4four {
  0% {
    transform: translateY(0px);
  }
  25% {
    transform: translateY(40px);
  }
  50% {
    transform: translateY(-30px);
  }
  75% {
    transform: translateY(80px);
  }
  100% {
    transform: translateY(0px);
  }
}

#dotX-1,
#dotX-2,
#dotX-3 {
  animation: dotX 3s infinite linear;
}

@keyframes dotX {
  0% {
    transform: translateX(0px);
  }
  50% {
    transform: translateX(180px);
  }
  70% {
    transform: translateX(180px);
  }
  100% {
    transform: translateX(0px);
  }
}

/* END OF SVG */
