.works {
  height: 100%;
  background-color: rgb(30, 1, 68);
  padding-bottom: 15vh;
  text-align: center;
  width: 100%;
  position: relative;
}
.w-t {
  padding-top: 100px;
}
.w-h {
  color: white;
}
.w-s {
  color: rgb(222, 223, 236);
}
.works-container {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: center;
  margin: 75px auto;
  width: 95%;
  min-height: 100vh;
}
.works-section {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.sites,
.designs,
.components {
  display: flex;
  flex-direction: row;
  justify-content: center;
  padding-bottom: 70px;
}
.components {
  padding-bottom: 150px;
}
.site-anim {
  animation: cards-1 1.5s linear;
}
@keyframes cards-1 {
  from {
    transform: translate(-100%);
  }
  to {
    transform: translate(0%);
  }
}

.design-anim {
  animation: cards-2 1.5s linear;
}
@keyframes cards-2 {
  from {
    transform: translate(100%);
  }
  to {
    transform: translate(0%);
  }
}
.movecompf {
  animation: movinganim 1s linear;
}
.movecomps {
  animation: movinganim 1.2s linear;
}
.movecompt {
  animation: movinganim 1.5s linear;
}

@keyframes movinganim {
  0% {
    transform: translateY(100px);
    opacity: 0;
  }
  100% {
    transform: translateY(0);
    opacity: 1;
  }
}

.works-sec-title {
  text-align: left;
  padding: 20px 20px;
}
.works-sec-heading {
  margin-bottom: 10px;

  font-size: 16px;
  color: #e4e8ec;
  font-weight: 600;
  text-transform: uppercase;
}
.works-sec-subtitle {
  font-size: 14px;
  line-height: 1.5;
  color: #d9dbdd;
}
.works-item {
  height: 295px;
  width: 295px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0px 25px;
  position: relative;
  overflow: hidden;
}
.works-item-title {
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 25;
}
.background-img {
  z-index: 30;
  position: relative;
}
.works-item__img {
  z-index: 35;
  position: absolute;
}
.works-item img {
  -webkit-transition: 0.25s ease-in-out;
  transition: 0.25s ease-in-out;
}
.hover-overlay {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  height: 100%;

  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;

  background-color: #e7ecff;
  letter-spacing: 0.5px;

  -webkit-transition: 0.5s ease-in-out;
  transition: 0.5s ease-in-out;
  opacity: 0;
}
.overlay-text {
  color: rgb(1, 5, 51);
  font-weight: 800;
  font-size: 18px;
  padding: 0px 20px 30px;
}

.works-item:hover img {
  -webkit-transform: scale(1.2);
  transform: scale(1.2);
  -webkit-transition: 0.25s ease-in-out;
  transition: 0.25s ease-in-out;
}

.works-item:hover .hover-overlay {
  opacity: 1;
  z-index: 50;
}
.tt-m {
  text-align: center;
}
.tt-r {
  text-align: right;
}
.ad-ps,
.pr-item,
.des-3 {
  width: 300px;
  height: 300px;
  opacity: 0.8;
}
.works-svg-anim {
  position: absolute;
  background-color: transparent;
  bottom: 0%;
  right: 5%;
  padding-bottom: 3vh;
}
#right-arm {
  animation: arm-moving 4s ease-in-out infinite;
  transform-origin: left;
  transform-box: fill-box;
}
@keyframes arm-moving {
  0% {
    transform: rotateZ(0deg);
  }
  33% {
    transform: rotateZ(-30deg);
  }
  66% {
    transform: rotateZ(-30deg);
  }
  100% {
    transform: rotateZ(0deg);
  }
}
#picture-2 {
  animation: picture-moving 4s ease-in-out infinite;
}
@keyframes picture-moving {
  0% {
    transform: translate(0, 0);
  }
  33% {
    transform: translate(-16px, -16px);
  }
  66% {
    transform: translate(-16px, -16px);
  }
  100% {
    transform: translate(0, 0);
  }
}
#picture-1 {
  animation: picture-move 4s ease-in-out infinite;
}
@keyframes picture-move {
  0% {
    transform: translate(0, 0);
  }
  33% {
    transform: translate(-12px, -10px);
  }
  66% {
    transform: translate(-12px, -10px);
  }
  100% {
    transform: translate(0, 0);
  }
}
#girl-head {
  animation: girl-head 4s ease-in-out infinite;
  transform-origin: bottom;
  transform-box: fill-box;
}
@keyframes girl-head {
  0% {
    transform: rotateZ(0deg);
  }
  33% {
    transform: rotateZ(-15deg);
  }
  66% {
    transform: rotateZ(-15deg);
  }
  100% {
    transform: rotateZ(0deg);
  }
}
#list-dot-1 {
  animation: ld 4s ease-in infinite;
}
#list-dot-2 {
  animation: ld 3s ease infinite;
}
#list-dot-3 {
  animation: ld 2s ease-in-out infinite;
}

@keyframes ld {
  0% {
    opacity: 0;
  }
  33% {
    opacity: 1;
  }
  66% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
#leaf-1 {
  animation: leaf-1 5s ease-out infinite;
  transform-origin: bottom;
  transform-box: fill-box;
}
@keyframes leaf-1 {
  0% {
    transform: rotate(0deg);
  }
  33% {
    transform: rotate(-15deg);
  }
  66% {
    transform: rotate(3deg);
  }
  100% {
    transform: rotate(0deg);
  }
}
#leaf-2 {
  animation: leaf-2 6s ease-out infinite;
  transform-origin: bottom;
  transform-box: fill-box;
}
@keyframes leaf-2 {
  0% {
    transform: rotate(0deg);
  }
  33% {
    transform: rotate(-7deg);
  }
  66% {
    transform: rotate(7deg);
  }
  100% {
    transform: rotate(0deg);
  }
}
/* End of Works
===================*/

/* Pricing
===================*/
.pricing {
  height: 100%;
  text-align: center;
  width: 100%;
  padding-bottom: 50px;
}
.p-t {
  padding-top: 40px;
}
.pricing-inner {
  display: flex;
  flex-direction: row;
  align-items: center;
}
.card-container {
  width: 33.333%;
  margin: 40px auto;
}
.card {
  color: white;
  padding: 40px 0;
  margin: 40px auto;
  transform-style: preserve-3d;
}
.card-body {
  transform: translateZ(20px);
}
.card-1,
.card-2,
.card-3 {
  background: linear-gradient(rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.5)),
    url(./assets/images/pricing-card.jpeg) center;
  background-size: cover;
  background-repeat: no-repeat;
  box-shadow: 0px 0px 50px #555;
  max-width: 375px;
}
.card-heading {
  text-transform: uppercase;
  font-size: 22px;
  letter-spacing: 0.2;
  font-weight: 600;
  margin-bottom: 20px;
}
.card-price {
  font-size: 60px;
  font-weight: 400;
}
.card-list {
  list-style-type: none;
  margin: 0;
  padding: 0;
}
.card-list-item {
  border-bottom: 1px groove #eee;
  width: 55%;
  margin: auto;
  font-weight: bold;
  padding: 15px 0;
}
.b-0 {
  border: none;
}
.prc-btn-container {
  margin: 20px;
}
.price-card-btn:hover {
  background: #fc9565;
  transform: translateY(-2px);
}

.moveFromLeft {
  animation: moveFromLeft 1.5s linear;
}
.moveFromRight {
  animation: moveFromRight 1.5s linear;
}
.moveFromBottom {
  animation: moveFromBottom 1.5s linear;
}

@keyframes moveFromLeft {
  0% {
    transform: translateX(-100px);
    opacity: 0;
  }
  100% {
    transform: translateX(0);
    opacity: 1;
  }
}

@keyframes moveFromRight {
  0% {
    transform: translateX(100px);
    opacity: 0;
  }
  100% {
    transform: translateX(0);
    opacity: 1;
  }
}

@keyframes moveFromBottom {
  0% {
    transform: translateY(100px);
    opacity: 0;
  }
  100% {
    transform: translateY(0);
    opacity: 1;
  }
}
/* End of Pricing
===================*/

#contact {
