  left: 0%;
  width: 50%;
  height: 100%;
  background: #03a9f4;
}
.contact-tt {
  position: absolute;
  z-index: 150;
  padding-top: 50px;
  margin-bottom: 50px;
  top: 0%;
}
.contact-h {
  color: white;
}
.contact-subtt {
  color: rgb(250, 245, 239);
}
#contact .container {
  position: relative;
  margin-top: 200px;
  margin-bottom: 100px;
  z-index: 100;
  display: flex;
  min-width: 1100px;
  min-height: 550px;
}
.contact-info {
  position: absolute;
  top: 40px;
  width: 350px;
  height: calc(100% - 80px);
  background: #0f3959;
  z-index: 1;
  padding: 40px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  box-shadow: 0 20px 20px rgba(0, 0, 0, 0.2);
}
.contact-info h2 {
  color: #fff;
  font-size: 24px;
  font-weight: 500;
}
.contact-info .info {
  position: relative;
  margin: 20px 0;
  margin-left: -40px;
}
.contact-info .info li {
  position: relative;
  list-style: none;
  display: flex;
  margin: 20px 0;
  cursor: pointer;
  align-items: flex-start;
}
.info li span:nth-child(1) {
  width: 30px;
  min-width: 30px;
}
.info li span:nth-child(1) img {
  max-width: 100%;
  opacity: 0.5;
  filter: invert(1);
}
.info li span:nth-child(2) {
  color: #fff;
  margin-left: 10px;
  font-weight: 300;
  opacity: 0.5;
}
.info li:hover span:nth-child(1) img,
.info li:hover span:nth-child(2) {
  opacity: 1;
}
.contact-info .sci {
  position: relative;
  display: flex;
  margin-left: -40px;
  padding-top: 100px;
}
.contact-info .sci li {
  list-style: none;
  margin-right: 15px;
}
.contact-info .sci li a {
  text-decoration: none;
}
.contact-info .sci li img {
  filter: invert(1);
  opacity: 0.5;
  width: 30px;
  height: 30px;
}
.contact-info .sci li:hover img {
  opacity: 1;
}

.contactForm {
  position: absolute;
  padding: 70px 50px;
  background: #fff;
  margin-left: 150px;
  padding-left: 250px;
  width: calc(100% - 150px);
  box-shadow: 0 50px 50px rgba(0, 0, 0, 0.5);
  height: 100%;
}
.contactForm h2 {
  color: #0f3959;
  font-size: 24px;
  font-weight: 500;
}
.formBox {
  position: relative;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  padding-top: 30px;
}
.formBox,
.inputBox {
  position: relative;
  margin: 0 0 35px 0;
}
.w50 {
  width: 47%;
}
.w100 {
  width: 100%;
}
.inputBox input,
textarea {
  width: 100% !important;
  resize: none;
  padding: 5px 0;
  font-size: 18px;
  font-weight: 300;
  color: #333;
  border: none;
  border-bottom: 1px solid #777;
  outline: none;
}
.inputBox textarea {
  min-height: 120px;
}
.inputBox span {
  position: absolute;
  left: 0;
  padding: 5px 0;
  font-size: 18px;
  font-weight: 300;
  color: #333;
  transition: 0.5s;
  pointer-events: none;
}
.inputBox input:focus ~ span,
textarea:focus ~ span,
.inputBox input:valid ~ span,
textarea:valid ~ span {
  transform: translateY(-20px);
  font-size: 12px;
  font-weight: 400;
  letter-spacing: 1px;
  color: #fe7a1a;
}
.inputBox input[type="submit"] {
  position: relative;
  cursor: pointer;
  color: black;
  border: none;
  max-width: 150px;
  padding: 12px;
  font-weight: bold;
  background-color: #fe7a1a;
  transition: all 0.3s linear;
}
.inputBox input[type="submit"]:hover {
  background-color: rgb(250, 148, 74);
  transform: translateY(5px);
}
.copyright {
