  font-size: 14px;
  line-height: 1.5;
  color: #fff;
}
.copyright__text a {
  color: #fdad73;
  text-decoration: none;
}
.loader {
  width: 100%;
  height: 100%;
  background-color: #fff;
  position: fixed;
  top: 0px;
  left: 0px;
  z-index: 100000000;
}

.loader .inner {
  width: 100%;
  height: 100%;
  background: url(./assets/loader.gif) center center no-repeat;
}

